
.header-color{
  background: #11998e;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #38ef7d, #11998e);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #38ef7d, #11998e); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.landing-grid{
  background: #11998e;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #38ef7d, #11998e);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #38ef7d, #11998e); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  text-align:center;
  position:fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.avatar-img{
  height: 250px;
  padding-top: 5em;
  border-radius: 50%;
}

.welcome{
  background-color: black;
  opacity: .8;
  width:75%;
  margin:auto;
  border-radius: 10px;
}

.welcome h1{
  font-size:5vw;
  color: white;
  font-weight: bold;
  font-family: "Cinzel";
}
.welcome h2{
  font-size:5vw;
  color: white;
  font-weight: bold;
  font-family: "Cinzel";
}
.welcome hr{
  border-top: 5px dotted white;
  width: 100%;
  margin: auto; 

}
.landing-grid .social-links{
  padding-left: 80%;
  display:flex;
  justify-content: space-between;
  width:10%;
  padding-top: 4em;
  margin:auto;
}

.landing-grid i{
  color:white;
  font-size: 4em;
}

@media screen and (max-width: 1024px) {

.landing-grid .social-links{
  padding-left: 0%; 
  display:flex;
  justify-content: space-between;
  width:50%;
  padding-top: 4em;
  margin:auto;
  }
.landing-grid i{
  color:white;
  font-size: 7vh;
  }
}

.work-grid p{
  font-size: 2vw;
  font-weight: bold;
}


.word-card-section  img{
  height:150%;
  width:100%;
}

.word-card-section h4{
  font-size: 3vh;
  font-weight: bold;

}

@media screen and (max-width: 1024px) {
.word-card-section  img{
  height:100%;
  width:110%;
  }
.word-card-section p{
  font-size: 3vh;
  font-weight: bold;
  }
.word-card-section h4{
  font-size: 2vh;
  font-weight: bold;

  }
.word-card-section .work-text{
  font-size: 2vh;
  }
}



